'use strict';

const btn = document.querySelector('#btn');
const usersInput = document.querySelector('#usersNumber');
const counter = document.querySelector('#counter');
const resultTable = document.querySelector('#resultTable');
const btnPlay = document.querySelector('#btnPlay');
const area = document.querySelector('#area');
const gameBack = document.querySelector('#gameBack');
const btnEnd = document.querySelector('#btnEnd');
const btnClose = document.querySelector('#btnClose');
const win = document.querySelector('#win');
const winPoint = document.querySelector('#winPoint');

btnPlay.addEventListener('click', () => {
  area.classList.add('area--active');
  gameBack.classList.add('game__back--active');
  play(counter);
});

btnEnd.addEventListener('click', () => {
  window.location.reload();
});

function play(counter) {
  counter.value = 0;
  let pcNumber = guessNumber();
  console.log('pc:', pcNumber);

  btn.addEventListener('click', () => {
    let usersNumber = usersInput.value;
    if (testUsersInput(usersNumber)) {
      counter.value++;
      addResultInTable(counter, usersNumber, testBullAndCow(pcNumber, usersNumber));
      if (usersNumber === pcNumber) won(counter.value);
      usersInput.value = '';

    } else {
      alert('only four-digit numbers, and only different numbers');
    }
  });
}

function testUsersInput(usersNumber) {
  if (usersNumber.length !== 4) return false;
  for (let el of usersNumber) {
    if (usersNumber.split(el).join('').length !== 3) return false;
  }
  return true;
}

function guessNumber() {
  let res = '';
  while (res.length !== 4) {
    let n = (Math.floor(Math.random() * 10)).toString();
    if (!res.includes(n)) res += n;
  }
  return res;
}

function testBull(pcNumber, usersNumber) {
  let counter = 0;
  for (let i = 0; i < pcNumber.length; i++) {
    if (pcNumber[i] === usersNumber[i]) {
      counter++;
    }
  }
  return counter;
}

function testCow(pcNumber, usersNumber) {
  let counter = 0;
  for (let el of usersNumber) {
    if (pcNumber.includes(el)) counter++;
  }
  return counter;
}

function testBullAndCow(pcNumber, usersNumber) {
  let bull = testBull(pcNumber, usersNumber);
  let cow = testCow(pcNumber, usersNumber) - bull;
  return {bull: bull, cow: cow};
}

function addResultInTable(counter, usersNumber, test) {
  resultTable.insertAdjacentHTML('afterend',
    `<tr>
              <td>${counter.value}</td>
              <td>${usersNumber}</td>
              <td>${test.bull}</td>
              <td>${test.cow}</td>
            </tr>`
  );
}

function won(point) {
  winPoint.value = point;
  area.classList.remove('area--active');
  win.classList.add('win--active');

  btnClose.addEventListener('click', () => {
    window.location.reload();
  });

}
